AAwB,EACzB,EAAA;;A5BtDC;E4B0DF;IAAoB,iBAAgB,EAAI;EACxC;IAAsB,mBAAkB,EAAI;EAC5C;IAAqB,kBAAiB,EAAI,EAAA","file":"styles.scss","sourcesContent":[".relative { position: relative; }\n.static { position: static; }\n.ib { display: inline-block; }\n.block { display: block; }\n.flex { display: flex; }\n\n.pull-left { float: left; }\n.pull-right { float: right; }\n\n.marginless { margin: 0 !important; }\n.paddingless { padding: 0 !important; }\n\n.no-transition { transition: none !important; }\n.no-transition-duration { transition-duration: 0ms !important; }\n\n.height-inherit { height: inherit; }\n.height-full { height: 100%; }\n.no-min-height { min-height: 0; }\n\n.width-inherit { width: inherit; }\n.width-full { width: 100%; }\n.no-min-width { min-width: 0; }\n\n.grab { cursor: grab; }\n\n.grab.grabbing,\n.grabbing,\n.grabbing .grab {\n  cursor: grabbing;\n}\n\n.unselectable {\n  user-select: none;\n}\n\n.flex-center {\n  display: flex;\n  align-items: center;\n  height: 100%;\n}\n\n.align-items-center { align-items: center; }\n.justify-content-center { justify-content: center; }\n\n.clearfix { @include clearfix(); }\n.fill-parent { @include fill-parent(); }\n\n.fade {\n  opacity: 0;\n  transition: opacity .15s linear;\n\n  &.in {\n    opacity: 1;\n  }\n}\n\n.background-image-cover {\n  @include fill-parent();\n  @include background-cover();\n  width: 100%;\n  height: 100%;\n}\n\n// Overflow hidden because polyfills will resize the image within.\n.image-cover { overflow: hidden; }\n.image-cover img {\n  width: 100%;\n  max-width: none;\n  height: 100%;\n  object-fit: cover;\n}\n\n// Place on an element (like the body) to prevent the mouse from scrolling it.\n// This does not work for touch devices.\n.no-overflow,\n.no-scroll {\n  overflow: hidden;\n}\n\n.off-screen {\n  position: absolute !important;\n  left: -9999px !important;\n}\n\n// Hide from both screenreaders and browsers: h5bp.com/u\n.hidden {\n  display: none !important;\n  visibility: hidden;\n}\n\n// Hide only visually, but have it available for screenreaders: h5bp.com/v\n.visuallyhidden {\n  border: 0;\n  clip: rect(0 0 0 0);\n  height: 1px;\n  margin: -1px;\n  overflow: hidden;\n  padding: 0;\n  position: absolute;\n  width: 1px;\n}\n\n// Hide visually and from screenreaders, but maintain layout\n.invisible {\n  visibility: hidden;\n}\n\n.unstyled-list {\n  list-style-type: none;\n  padding: 0;\n  margin: 0;\n}\n\n.unstyled-button {\n  padding: 0;\n  background: none;\n  border: 0;\n}\n","\n// @param {string} bp Breakpoint value. One of `xs, sm, md, lg`.\n// @param {boolean} isMaxWidth By default, the media queries are mobile first,\n//     so they use `min-width: __px`. By passing `true`, the mixin will subtract\n//     one pixel from the breakpoint value and make it `max-width: __px`.\n// @param {boolean} isScreenOnly Whether to hide this media query from print styles.\n//\n// Note: For print media, we want the default styles and the xs breakpoint to take effect.\n@mixin breakpoint($bp, $isMaxWidth: false, $isScreenOnly: true) {\n  // Avoid media queries like (min-width: 0px) and (max-width: -1px).\n  @if $bp == xs {\n    @content;\n  } @else {\n    $query: get-breakpoint-query($bp, $isMaxWidth);\n\n    @media #{get-media-query($query, $isScreenOnly)} {\n      @content;\n    }\n  }\n}\n\n@mixin breakpoints($min, $max) {\n  $start: map-get($breakpoints, $min);\n  $end: map-get($breakpoints, $max);\n\n  @if map-has-key($breakpoints, $min) and map-has-key($breakpoints, $max) {\n    $end: $end - 1px;\n    @if $start > $end {\n      @warn \"Unreachable media query: #{$start} <--> #{$end}\";\n      @content;\n    } @else {\n\n      $media-query-min: \"(min-width: #{$start})\";\n      $media-query-max: \"(max-width: #{$end})\";\n\n      // There should be no lower bound when the extra small breakpoint is used.\n      // e.g. `@include breakpoints(xs, sm)` should be from 0 <--> 768.\n      @if $min == xs {\n        @media screen and #{$media-query-max} {\n          @content;\n        }\n      } @else {\n        @media screen and #{$media-query-min} and #{$media-query-max} {\n          @content;\n        }\n      }\n    }\n  } @else {\n    @warn \"#{$min} or #{$max} breakpoint not recognized. Valid breakpoints: #{map-keys($breakpoints)}\";\n    @content;\n  }\n}\n\n@mixin augment-hit-area($byPercent: 100%, $pseudo: 'after') {\n  $isPercent: str-slice(inspect($byPercent), -1) == \"%\";\n  $size: if($isPercent, 100% + $byPercent, $byPercent);\n  $offset: if($isPercent, $byPercent / -2, $byPercent / -4);\n\n  // Increase the hit area by 100% by placing a pseudo element behind the handle\n  &::#{$pseudo} {\n    content: '';\n    position: absolute;\n    // Having the hit area on top of the handle seemed to help Android a little. Was below it (-1)\n    z-index: 1;\n    top: $offset;\n    left: $offset;\n    display: block;\n    width: $size;\n    height: $size;\n\n    // Uncomment to see the hit area\n    // background-color: rgba(255, 255, 0, 0.3);\n  }\n}\n\n@mixin keep-aspect() {\n  position: relative;\n  width: 100%;\n  height: 0;\n  overflow: hidden;\n  padding-bottom: 100%;\n}\n\n@mixin aspect($width, $height) {\n  padding-bottom: percentage($height / $width);\n}\n\n@mixin no-aspect() {\n  height: auto;\n  padding-bottom: 0;\n  overflow: visible;\n}\n\n// Fill the parent using absolute positioning\n@mixin fill-parent() {\n  position: absolute;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n}\n\n// For when you need fill-parent on one breakpoint, but not a bigger one.\n@mixin reset-fill-parent() {\n  position: static;\n  top: auto;\n  right: auto;\n  bottom: auto;\n  left: auto;\n}\n\n\n// Clearfix\n// --------\n// For clearing floats like a boss h5bp.com/q\n@mixin clearfix() {\n  &::after {\n    content: \"\";\n    display: table;\n    clear: both;\n  }\n}\n\n// Text overflow\n// -------------------------\n// Requires inline-block or block for proper styling\n@mixin text-overflow($nowrap: true) {\n  overflow: hidden;\n  text-overflow: ellipsis;\n  @if ($nowrap) {\n    white-space: nowrap;\n  }\n}\n\n\n@mixin font-smooth($smooth: true) {\n  @if $smooth {\n    -webkit-font-smoothing: antialiased;\n    -moz-osx-font-smoothing: grayscale;\n  }\n  @else {\n    -webkit-font-smoothing: subpixel-antialiased;\n    -moz-osx-font-smoothing: auto;\n  }\n}\n\n// Promoting layers can alleviate many animation issues, but also be\n// aware that having too many layers will cause performance issues.\n// http://jankfree.org/\n//\n// Prefer `will-change`\n// https://developer.mozilla.org/en-US/docs/Web/CSS/will-change\n// http://aerotwist.com/blog/bye-bye-layer-hacks/\n@mixin layer-promote() {\n  transform: translateZ(0);\n}\n\n@mixin background-cover() {\n  background-size: cover;\n  background-repeat: no-repeat;\n  background-position: center center;\n}\n\n@mixin size($width, $height: $width) {\n  width: $width;\n  height: $height;\n}\n\n@mixin table-center-wrap() {\n  display: table;\n  table-layout: fixed;\n  height: 100%;\n  width: 100%;\n}\n\n@mixin table-center() {\n  display: table-cell;\n  vertical-align: middle;\n}\n","// http://www.smashingmagazine.com/2013/03/tips-and-tricks-for-print-style-sheets/\n\n// Viewport sizes (as of Nov. 2015):\n// Chrome: 550px\n// Firefox: 810px\n// Safari: based on the size of the viewport when you hit print.\n// IE9: 670px\n// Edge: 670px\n\n@media print {\n  .hidden\\@print {\n    display: none !important;\n  }\n\n  .spacer-top-none\\@print {\n    padding-top: 0;\n  }\n\n  .spacer-btm-none\\@print {\n    padding-bottom: 0;\n  }\n\n  html {\n    background-color: white;\n  }\n\n  main {\n    overflow: visible;\n  }\n\n  body {\n    width: 100%;\n  }\n\n  // Margins are customizable if desired.\n  // @page {\n  //   margin: 2cm;\n  // }\n\n  // Prevent headings from being printed at the bottom of the page.\n  h2,\n  h3 {\n     page-break-after: avoid;\n  }\n\n  // Prevent large elements, such as unordered lists and images, from being split\n  // across multiple pages.\n  .tile,\n  ul,\n  img {\n     page-break-inside: avoid;\n  }\n}\n","@font-face {\n  font-family: 'Posterama';\n  src: url('/src/fonts/Posterama1984-Bold.woff') format('opentype');\n}\n@font-face {\n  font-family: 'Avant Garde Bold';\n  src: url('/src/fonts/AvantGardeStd-Bk.woff') format('opentype');\n}\n@font-face {\n  font-family: 'Avant Garde Demi';\n  src: url('/src/fonts/AvantGardeStd-Demi.woff') format('opentype');\n}\n","/* Required styles */\n\n.odo-carousel {\n  position: relative;\n}\n\n.odo-carousel__wrapper {\n  position: relative;\n  z-index: 1;\n  width: 100%;\n  height: 400px;\n}\n\n.odo-carousel__element {\n  position: relative;\n  width: 2000%;\n  height: inherit;\n  padding: 0;\n  margin: 0;\n\n  /* You can disable selection on the carousel */\n  /*\n  -webkit-user-select: none;\n     -moz-user-select: none;\n      -ms-user-select: none;\n          user-select: none;\n  */\n}\n\n/* Clearfix for the slides */\n.odo-carousel__element::before,\n.odo-carousel__element::after {\n  content: '';\n  display: table;\n}\n.odo-carousel__element::after {\n  clear: both;\n}\n\n.odo-carousel__slide {\n  float: left;\n  display: block;\n  width: 5%;\n  height: 100%;\n  /* Prevent slides from collapsing, making calculations incorrect. */\n  min-height: 1px;\n  overflow: hidden;\n}\n\n.odo-carousel--vertical .odo-carousel__wrapper {\n  overflow: hidden;\n}\n\n.odo-carousel--vertical .odo-carousel__element {\n  width: auto;\n  height: 100%;\n}\n\n.odo-carousel--vertical .odo-carousel__element .odo-carousel__slide {\n  float: none;\n  width: auto;\n}\n\n.odo-carousel--fade .odo-carousel__element {\n  width: auto;\n}\n\n.odo-carousel--fade .odo-carousel__slide {\n  position: relative;\n  z-index: 1;\n  float: left;\n  width: 100%;\n  height: 100%;\n  margin-right: -100%; /* has the effect of stacking the slides */\n  opacity: 0;\n}\n\n.odo-carousel--fade .odo-carousel__slide--behind {\n  z-index: 2;\n}\n\n.odo-carousel--fade .odo-carousel__slide--visible {\n  opacity: 1;